:root{
  --primary:#d81e5b;
  --secondary:#8c38ff;
  --dark:#131a26;
  --light:#eee;
}

* {
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family:'Fira Sans',sans-serif;
}

body{
  background-color:var(--secondary);
  text-align: center;
  font-weight: 900;
  font-family: 'Fira Sans', sans-serif;
}

.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
  
}

.calculator{
  width:100%;
  max-width: 400px;
  background-color: #FFF;
  border-radius: 16px;
  overflow: hidden;
 box-shadow: 0 2px 64px rgba(0,0,0,0.2);
}

.display{
  padding: 16px;
  background-color: var(--dark);
  color: var(--light);
  font-size: 2rem;
  text-align: right;

}
.display span{
  font-size: 14px;
  color: #888888;
}

button{
  appearance: none;
  border:none;
  outline: none;
  color:#FFF;
  font-size: 20px;
  padding: 20px;
  cursor:pointer;
  transition: 0.4;
}

button:hover{
  opacity: 0.9;
}

.operator{
  display: flex;
}

.operator button{
  flex : 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}

.digit{
  display: flex;
  flex-wrap: wrap;
}

.digit button{
  flex : 1 1 33.3333%;
  max-width: 33.3333;
  background-color: var(--dark);
}
